body,
html {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  height: 100%;
  background-color: #343a40;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  max-width: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
}
.h100 {
  height: 100%;
}
.list-item-active {
  font-weight: bold;
  color: #303f9f !important;
  background: rgba(0, 0, 0, 0.04);
}
.border-left {
  border-left: 4px solid #303f9f !important;
}
.centered {
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -50px;
  margin-left: -50px;
}
.centered:before {
  content: "";
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
table {
  width: 100%;
}
.mat-spinner {
  z-index: 1000;
}
/***  Fonts ***/
.f-50 {
  font-size: 50px;
}
.f-small {
  font-size: 1rem;
}
.f-medium {
  font-size: 1.5rem;
}
.f-large {
  font-size: 2rem;
}
.float-right {
  float: right;
}
.float-left {
  float: left;
}
.notify-success {
  color: white !important;
  background-color: green !important;
  border-color: green !important;
  font-size: 20px;
}
.notify-success .mat-simple-snackbar-action {
  color: white !important;
}
.mat-drawer-content {
  overflow: hidden !important;
}
/**** Drag ****/
/* in-flight clone */
.gu-mirror {
  position: fixed !important;
  margin: 0 !important;
  z-index: 9999 !important;
  opacity: 0.8;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";
  filter: alpha(opacity=80);
  pointer-events: none;
}
/* high-performance display:none; helper */
.gu-hide {
  left: -9999px !important;
}
/* added to mirrorContainer (default = body) while dragging */
.gu-unselectable {
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  user-select: none !important;
}
/* added to the source element while its mirror is dragged */
.gu-transit {
  opacity: 0.2;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=20)";
  filter: alpha(opacity=20);
}
table {
  width: 100%;
}
table .mat-table-sticky {
  z-index: 0 !important;
}
.table-container {
  overflow: auto;
  max-height: 500px;
}